AA,CAEA,cAAA,CAGF,2BACE,iBAAA,CACA,kBAAA,CACA,eAAA,CACA,qBAAA,CACA,6CAAA,CACA,4BAAA,CAGF,8BACE,iBAAA,CACA,mBAAA,CACA,eAAA,CACA,qBAAA,CACA,gDAAA,CACA,4BAAA,CAIJ,wBACE,KACE,uBAAA,CAEF,GACE,uCAAA,CAAA,CAIJ,2BACE,KACE,uBAAA,CAEF,GACE,kDAAA,CAAA,CAIJ,wBACE,KACE,kBAAA,CAGF,GACE,kBAAA,CAAA","sourcesContent":["// 播放器框架\r\n@import \"./theme\";\r\n\r\n.#{$prefix} {\r\n  width: 100%;\r\n  height: 100%;\r\n  font-size: 12px;\r\n  &-main {\r\n    position: relative;\r\n    width: 100%;\r\n    height: calc(100% - $footbar-height);\r\n    overflow: hidden;\r\n  }\r\n  // 视频展示区\r\n  &-video-area {\r\n    position: relative;\r\n    width: 100%;\r\n    height: 100%;\r\n    // 视频容器\r\n    .#{$prefix}-video-wrap {\r\n      width: 100%;\r\n      height: 100%;\r\n      position: relative;\r\n      display: flex;\r\n      justify-content: center;\r\n      align-items: center;\r\n      background: black;\r\n      box-sizing: border-box;\r\n      video {\r\n        display: block;\r\n        width: 100%;\r\n        height: 100%;\r\n      }\r\n    }\r\n  }\r\n  &-head-wrap {\r\n    position: absolute;\r\n  }\r\n  &-side-wrap {\r\n    position: absolute;\r\n    width: 100%;\r\n    height: 100%;\r\n    top: 0;\r\n    left: 0;\r\n  }\r\n  &-modal-wrap {\r\n    position: absolute;\r\n    width: 100%;\r\n    height: 100%;\r\n    top: 0;\r\n    left: 0;\r\n  }\r\n  &-contextmenu-wrap {\r\n    position: absolute;\r\n    width: 100%;\r\n    height: 100%;\r\n    top: 0;\r\n    left: 0;\r\n  }\r\n  &-footbar {\r\n    height: $footbar-height;\r\n    background-color: $bg-white;\r\n  }\r\n  &-danmaku-wrap {\r\n    position: absolute;\r\n    left: 0;\r\n    top: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    > div {\r\n      position: absolute;\r\n      left: 0;\r\n      top: 0;\r\n      width: 100%;\r\n      height: 100%;\r\n    }\r\n  }\r\n\r\n  &-tooltip {\r\n    position: absolute;\r\n    left: 50%;\r\n    top: -30px;\r\n    transform: translateX(-50%);\r\n    white-space: nowrap;\r\n    display: none;\r\n    height: 20px;\r\n    line-height: 20px;\r\n    padding: 0 4px;\r\n    font-size: 12px;\r\n    background-color: $panel-black;\r\n    color: $text-white;\r\n    border-radius: $border-radius;\r\n  }\r\n  li {\r\n    list-style: none;\r\n  }\r\n  &.mode-webfull {\r\n    z-index: 233333;\r\n    .#{$prefix} {\r\n      &-main {\r\n        position: fixed;\r\n        top: 0;\r\n        bottom: 0;\r\n        left: 0;\r\n        right: 0;\r\n        z-index: 233333;\r\n        height: 100%;\r\n      }\r\n    }\r\n  }\r\n}\r\n","// 主题变量\r\n$prefix: \"mfuns-player\";\r\n\r\n$bg-white: var(--mp-bg-white, #ffffff); // 背景白色\r\n$bg-black: var(--mp-bg-black, #202020) #202020; // 背景暗色\r\n\r\n$panel-white: var(--mp-panel-white, #ffffff); // 面板白色\r\n$panel-black: var(--mp-panel-black, #202020a0); // 面板黑色\r\n$panel-white-active: var(--mp-panel-white-active, #eeeeee); // 面板白色\r\n$panel-black-active: var(--mp-panel-black-active, #606060a0); // 面板黑色\r\n\r\n$text-black: #404040; // 文字黑色\r\n$text-grey: #808080; // 文字灰色\r\n$light-grey: #e6e6e6; // 淡灰色\r\n\r\n$text-white: #ffffffe0; // 文字白色\r\n$text-semiwhite: #ffffff80; // 文字半透明白色\r\n$light-semiwhite: #ffffff40; // 淡半透明白色\r\n\r\n$primary-color: var(--mp-primary-color, #7b7ff7); // 主要颜色\r\n$secondary-color: var(--mp-secondary-color, $primary-color); // 次要颜色\r\n\r\n$border-radius: var(--mp-border-radius, 4px); // 圆角大小\r\n\r\n$footbar-height: 40px; // 底栏高度\r\n$controller-fixed-height: 36px; // 固定控制栏高度\r\n","// 控制栏\r\n@import \"~@css/theme\";\r\n\r\n.#{$prefix} {\r\n  &-controller-wrap {\r\n    position: absolute;\r\n    bottom: -50px;\r\n    left: 0;\r\n    right: 0;\r\n    height: 50px;\r\n    transition: bottom 0.4s ease;\r\n  }\r\n  &-controller-mask {\r\n    opacity: 0;\r\n    position: absolute;\r\n    background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAADGCAYAAAAT+OqFAAAAdklEQVQoz42QQQ7AIAgEF/T/D+kbq/RWAlnQyyazA4aoAB4FsBSA/bFjuF1EOL7VbrIrBuusmrt4ZZORfb6ehbWdnRHEIiITaEUKa5EJqUakRSaEYBJSCY2dEstQY7AuxahwXFrvZmWl