"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
class Events {
    constructor() {
        this.events = {};
        this.playerEventList = [
            // 视频播放
            "play",
            "pause",
            "seeking",
            "seeked",
            "volume_change",
            "rate_change",
            "loop",
            "loop_off",
            // "time_update", // 播放时间更新
            // "progress", // 缓冲更新
            // 播放模式
            "source_change",
            "part_change",
            "video_change",
            // 播放器模式
            "fullscreen",
            "fullscreen_exit",
            "pip",
            "pip_exit",
            "wide",
            "wide_exit",
            "webfull",
            "webfull_exit",
            // 弹幕事件
            "danmaku:load_start",
            "danmaku:load_end",
            "danmaku:load_fail",
            "danmaku:load_addition_start",
            "danmaku:load_addition_end",
            "danmaku:load_addition_fail",
            "danmaku:load_new_start",
            "danmaku:load_new_end",
            "danmaku:load_new_fail",
            "danmaku:advanced_detected",
            "danmaku:show",
            "danmaku:hide",
            "danmaku:send",
            "danmaku:send_success",
            "danmaku:send_fail",
            // "danmaku:report",     // 举报弹幕
            "danmaku:filter",
            "danmaku:add",
            // 界面事件
            "resize",
            "contextmenu:show",
            "contextmenu:hide",
            "toast:show",
            "toast:hide",
            "darkmode",
            "darkmode_off",
            "fixedcontroller",
            "fixedcontroller_off",
            "setting:player",
            "setting:danmaku", // 弹幕设置项更改
        ];
        this.customEventList = [];
    }
    /** 添加监听 */
    on(name, listener) {
        if (this.checkEventName(name) && typeof listener === "function") {
            if (!this.events[name]) {
                this.events[name] = [];
            }
            this.events[name].push(listener);
        }
    }
    /** 移除监听 */
    off(name, listener) {
        if (this.checkEventName(name) && typeof listener === "function") {
            if (!this.events[name]) {
                this.events[name] = [];
            }
            const index = this.events[name].indexOf(listener);
            if (typeof index === "number")
                this.events[name].splice(index, 1);
        }
    }
    /** 触发事件 */
    trigger(name, ...args) {
        if (this.events[name] && this.events[name].length) {
            for (let i = 0; i < this.events[name].length; i++) {
                this.events[name][i](...args);
            }
        }
    }
    /** 注册一个自定义事件 */
    registerCustomEvent(name) {
        if (this.checkEventName(name))
            return;
        this.customEventList.push(name);
    }
    /** 检测事件名是否有效 */
    checkEventName(name) {
        if (this.playerEventList.indexOf(name) > -1 || this.customEventList.indexOf(name) > -1) {
            return true;
        }
        return false;
    }
}
exports.default = Events;
